/** Word rotation start **/
.animation_word-rotation-mask .animated_text {
  display: block;
  box-sizing: border-box;
  position: absolute;
  top: 60px;
}
.animation_word-rotation-mask span.animated_text[data-show] {
  top: 0;
  transform: translateY(-10%);
  transition: 0.5s all ease-in-out;
}
.animation_word-rotation-mask span.animated_text[data-up] {
  transform: translateY(-200%);
  transition: 0.5s all ease-in-out;
}

@media screen and (max-width: 526px) and (min-width: 480px) {
  .animation_word-rotation-mask.for-homeowners-hero {
    height: 110px;
  }
  .animation_word-rotation-mask .animated_text {
    top: 110px;
  }
  .animation_word-rotation-mask span.animated_text[data-show] {
    top: 0;
  }
  .display-block-mobile {
    display: block;
  }
}
@media screen and (max-width: 479px) {
  .animation_word-rotation-mask .animated_text {
    top: 50px;
  }
}
/** Word rotation end **/
/** Homeowners' hero section start **/
@media screen and (max-width: 767px) and (min-width: 480px) {
  .section_homeowners-hero .section_subheader {
    max-width: 80%;
  }
}
/** Homeowners' hero section end **/
/** Pricing and features section start **/
@media screen and (max-width: 690px) {
  .table_pricing-mobile .table-pricing_columns-wrapper .table-pricing_columns-lists {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
  }
}
/** Pricing and features section end **/
/** Property management section start **/
@media screen and (max-width: 520px) and (min-width: 480px) {
  .property-management_features {
    grid-template-columns: 1fr;
  }
  .property-management_headers {
    max-width: 100%;
  }
}
/** Property management section end **/